.card{
    display: flex;
    border: 2.5px solid black;
    width: 300px;
    margin: 10px 70px;
    border-radius: 12px;
    box-shadow: 1px 1px 1px 1px rgba(0, 0, 0, .5);
}

img{
    width: 6rem;
    padding: 1rem;
}

a{
    padding: 50px 70px;
    font-family: 'Segoe UI';
    font-weight: bold;
    text-decoration: none;
    color: white;
}